.App {
  font-family: sans-serif;
  text-align: center;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

canvas {
  touch-action: none;
}

#html-overlay {
  position: absolute;
  z-index: 1000;
  bottom: 0;
  left: 0;
  padding: 1rem 2rem;
  user-select: none;
  touch-action: none;
}

#controls {
  font-size: 4rem;
  display: flex;
  align-items: flex-end;
  margin: 1rem 0;
  user-select: none;
}

#controls .icon {
  pointer-events: none;
  /* background-color: white; */
  color: white;
}

#controls .btn {
  /* background-color: white; */
  transition: 0.4s all ease;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
}

#controls .pressed {
  transform: scale(0.5);
}

#controls .btn:hover {
  user-select: none;
  color: white;
  opacity: 0.5;
  transform: scale(0.95);
}

#controls .center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#instruction {
  text-transform: capitalize;
  color: white;
  font-family: sans-serif;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  padding: 5px 10px;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

#btns {
  position: absolute;
  top: 0;
  right: 0;
  margin: 20px;
  z-index: 100;
}

